.sidebar {
    position: fixed;
    top: var(--header-height);
    left: 0;
    height: calc(100vh - var(--header-height));
    width: var(--sidebar-mini-width);
    background: #ffffff;
    border-right: 1px solid var(--border-color);
    transition: width 0.2s cubic-bezier(0.4, 0, 0.2, 1);
    overflow: hidden;
    z-index: 100;
}

.sidebar.open {
    width: var(--sidebar-full-width);
}

.sidebar-content {
    width: var(--sidebar-full-width);
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
}

.sidebar-header {
    position: absolute;
    top: 1rem;
    right: -40px;
    z-index: 1;
}

.sidebar-toggle {
    width: 40px;
    height: 40px;
    background: #ffffff;
    border: 1px solid #e0e0e0;
    border-left: none;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 4px;
    box-shadow: 2px 0 4px rgba(0,0,0,0.05);
}

.sidebar-toggle span {
    display: block;
    width: 18px;
    height: 2px;
    background-color: #333;
    transition: all 0.3s ease;
}

.sidebar-section {
    margin-bottom: 2rem;
}

.sidebar-section h3 {
    font-size: 0.9rem;
    text-transform: uppercase;
    color: #333;
    margin-bottom: 1rem;
    padding-bottom: 0.5rem;
    border-bottom: 1px solid var(--border-color);
}

.sidebar-section ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.sidebar-section li {
    margin-bottom: 0.5rem;
}

.sidebar-section a {
    display: block;
    padding: 0.75rem 1rem;
    color: #333;
    text-decoration: none;
    border-radius: 4px;
    transition: background-color 0.2s ease;
}

.sidebar-section a:hover {
    background-color: #f8f9fa;
    color: #007bff;
}

.nav-item {
    position: relative;
    display: flex;
    align-items: center;
    padding: 0 12px;
    height: 40px;
    color: var(--text-primary);
    text-decoration: none;
    transition: background-color 0.2s;
}

.nav-item:hover {
    background-color: var(--hover-bg);
}

.nav-item.active {
    background-color: rgba(0, 0, 0, 0.05);
    font-weight: 500;
}

.nav-icon {
    min-width: 48px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.25rem;
    color: var(--text-primary);
}

.nav-label {
    flex: 1;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 0.875rem;
    transition: opacity 0.2s;
}

.nav-active-indicator {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 3px;
    height: 20px;
    background-color: var(--primary-color);
    border-radius: 0 2px 2px 0;
}

.section-header {
    padding: 16px 12px 8px;
    font-size: 0.75rem;
    font-weight: 500;
    text-transform: uppercase;
    color: var(--text-secondary);
    transition: opacity 0.2s;
}

/* Mini sidebar states */
.sidebar:not(.open) .nav-label,
.sidebar:not(.open) .section-header {
    opacity: 0;
}

.sidebar:not(.open) .nav-item {
    justify-content: center;
}

.sidebar:not(.open) .nav-icon {
    min-width: 24px;
}

@media (max-width: 768px) {
    .sidebar {
        transform: translateX(-100%);
    }
    
    .sidebar.open {
        transform: translateX(0);
        width: 240px;
    }
}