.site-header {
    height: var(--header-height);
    background: #ffffff;
    border-bottom: 1px solid var(--border-color);
    box-shadow: var(--shadow-sm);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    width: 100%;
}

.header-content {
    height: 100%;
    padding: 0 2rem;
    display: grid;
    grid-template-columns: auto 1fr auto;
    align-items: center;
    gap: 2rem;
}

/* Sidebar Toggle Button */
.sidebar-toggle {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 4px;
    width: 32px;
    height: 32px;
    padding: 0;
    background: none;
    border: none;
    cursor: pointer;
}

.sidebar-toggle span {
    display: block;
    width: 16px;
    height: 2px;
    background-color: var(--primary-color);
    transition: transform 0.3s ease;
}

.sidebar-toggle.active span:nth-child(1) {
    transform: translateY(6px) rotate(45deg);
}

.sidebar-toggle.active span:nth-child(2) {
    opacity: 0;
}

.sidebar-toggle.active span:nth-child(3) {
    transform: translateY(-6px) rotate(-45deg);
}

/* Logo */
.logo {
    display: flex;
    align-items: center;
    gap: 1rem;
    text-decoration: none;
}

.logo img {
    height: 40px;
    width: auto;
    object-fit: contain;
}

.logo-text {
    display: flex;
    flex-direction: column;
}

.logo-text h1 {
    color: var(--primary-color);
    font-size: 1.25rem;
    font-weight: 600;
    margin: 0;
}

.logo-text span {
    color: #666;
    font-size: 0.875rem;
}

/* Navigation */
.main-nav {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 2rem;
}

.nav-link {
    text-decoration: none;
    color: var(--primary-color);
    font-weight: 500;
    padding: 0.5rem;
    transition: color 0.2s ease;
    position: relative;
}

.nav-link:hover {
    color: #007bff;
}

.nav-link::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #007bff;
    transform: scaleX(0);
    transition: transform 0.2s ease;
}

.nav-link:hover::after {
    transform: scaleX(1);
}

/* Hide mobile menu toggle by default */
.mobile-menu-toggle {
    display: none;
}

@media (max-width: 768px) {
    .header-content {
        grid-template-columns: auto 1fr auto;
        padding: 0 1rem;
    }

    .sidebar-toggle {
        display: none;
    }

    .logo-text span {
        display: none;
    }

    .main-nav {
        display: none;
    }

    .main-nav.mobile-open {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: var(--header-height);
        left: 0;
        width: 100%;
        background: white;
        padding: 1rem;
        box-shadow: var(--shadow-sm);
        z-index: 999;
    }

    .mobile-menu-toggle {
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 4px;
        width: 30px;
        height: 30px;
        padding: 0;
        background: none;
        border: none;
        cursor: pointer;
    }
}