.app-layout {
    min-height: 100vh;
    display: grid;
    grid-template-areas:
        "header header"
        "sidebar main"
        "footer footer";
    grid-template-rows: var(--header-height) 1fr auto;
    grid-template-columns: var(--sidebar-width) 1fr;
    height: 100%;
    overflow: hidden;
}

.site-header {
    grid-area: header;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background: white;
    border-bottom: 1px solid var(--border-color);
    box-shadow: var(--shadow-sm);
    z-index: 1000;
}

.sidebar {
    grid-area: sidebar;
    height: calc(100vh - var(--header-height));
    margin-top: var(--header-height);
    background: #ffffff;
    border-right: 1px solid var(--border-color);
    overflow-y: auto;
    transition: transform 0.3s ease;
}

.content-wrapper {
    grid-area: main;
    padding: 2rem;
    margin-top: var(--header-height);
    width: 100%;
}

.footer {
    grid-area: footer;
    padding: 1rem;
    background: white;
    border-top: 1px solid var(--border-color);
    text-align: center;
}

/* Sidebar states */
.app-layout.sidebar-open {
    grid-template-columns: var(--sidebar-width) 1fr;
}

.app-layout:not(.sidebar-open) {
    grid-template-columns: 0 1fr;
}

.app-layout:not(.sidebar-open) .sidebar {
    transform: translateX(-100%);
}

/* Scrollable areas */
.sidebar {
    overflow-y: auto;
}

@media (max-width: 768px) {
    .app-layout {
        grid-template-areas:
            "header"
            "main"
            "footer";
        grid-template-columns: 1fr;
    }

    .app-layout.sidebar-open {
        grid-template-columns: 1fr;
    }

    .sidebar {
        position: fixed;
        left: 0;
        width: var(--sidebar-width);
        top: var(--header-height);
        bottom: 0;
        z-index: 999;
    }
}